import React from 'react';
import {Row, Col} from "antd";
import {Products} from "../../../../constants/products";
import Slider from "react-slick";
import {Card} from 'antd';
import {
    FaStar,
} from 'react-icons/fa';


const {Meta } = Card;
class NewProducts extends React.Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
        this.slider.slickNext();
    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            autoplay:false,
            slidesToShow: 5,
            slidesToScroll: 1,
            initialSlide: 0,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 1,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return(
            <>
                <div className="container">
                    <Row>
                        <p className="most-title">
                            Yangi mahsulotlar
                        </p>

                        <Col span={24} className={'p10'}>
                            <Slider {...settings}>
                                {
                                    Products.map(item=>{
                                        return <div className={'slider-item p10'}>
                                            <Card
                                                hoverable
                                                style={{ width: '100%', borderRadius:'10px'}}
                                                cover={<img width={'100%'} alt={item.name} src={item.imgUrl} />}
                                            >
                                                <Meta description={item.brand} />
                                                <p style={{minHeight:'90px',fontSize:'16px'}}>{item.name}</p>


                                                <table>
                                                    <tr>
                                                        <th><FaStar style={{color:'grey'}}/></th>
                                                        <th><FaStar style={{color:'grey'}}/></th>
                                                        <th><FaStar style={{color:'grey'}}/></th>
                                                        <th><FaStar style={{color:'grey'}}/></th>
                                                        <th><FaStar style={{color:'grey'}}/></th>
                                                    </tr>
                                                </table>
                                                <p style={{color:'grey',fontSize:'13px'}}>
                                                    {item.creditPrice} so'm/oy
                                                </p>
                                                <p style={{fontWeight:600}}>{item.newPrice} so'm</p>

                                            </Card>
                                        </div>
                                    })
                                }



                            </Slider>

                        </Col>
                    </Row>
                </div>

            </>
        )
    }

}
export default NewProducts