import React from 'react';
import '../Carousel/Carousel.css'
import {Row, Col} from "antd";
import Slider from "react-slick";
import {Card} from 'antd';
import {
    FaStar,
} from 'react-icons/fa';
import {CategoryList} from "../../../../constants/categoryList";


const {Meta } = Card;
class Categories extends React.Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
        this.slider.slickNext();
    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            autoplay:false,
            slidesToShow: 4,
            slidesToScroll: 1,
            initialSlide: 0,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return(
            <>
                <div className="container">
                    <Row>
                        <p className="most-title">
                            Kategoriyalar
                        </p>

                        <Col span={24} className={'p10'}>
                            <Slider {...settings}>
                                {
                                    CategoryList.map(item=>{
                                        return <div className={'slider-item p10'}>
                                            <Card
                                                className="category-card"
                                                style={{ width: '100%',height:'100px',borderRadius:'20px' }}
                                               >
                                                <Row>
                                                    <Col span={8}>
                                                        <img src={item.imgUrl} alt={item.title}/>
                                                    </Col>
                                                    <Col span={16}>
                                                        <div width={'100%'}>
                                                            <p className={'category-title'}>{item.title.length>15?item.title.slice(0,15)+'...':item.title}</p>
                                                        </div>
                                                    </Col>
                                                </Row>
                                            </Card>
                                        </div>
                                    })
                                }



                            </Slider>

                        </Col>
                    </Row>
                </div>

            </>
        )
    }

}
export default Categories