import React from 'react';
import './CreditTopCard.css';
import '../../../../styles/global.css'

class CreditTopCard extends React.Component{
    render() {
        return(
            <>
                <div className="container">
                    <div className="top-card-credit">
                        <p className="title">
                            Muddatli to'lov
                        </p>
                        <p className={' little-title'}>
                            Konditsionerlar muddatli to'lovga eng yaxshi narxlarda
                        </p>
                        <img className={'top-card-credit-img'} src="/images/topCreditPost/topCreditPost.png" alt="error"/>
                        <button className='more-details-button button'>
                            Batafsil
                        </button>
                    </div>
                </div>
            </>
        )
    }

}
export default CreditTopCard