import React from 'react';
import './topCard.css';
import '../../../../styles/global.css'

class TopCard extends React.Component{
    render() {
        return(
            <>
                <div className="container">
                    <div className="top-card">
                       <p className="title">
                           Konditsionerlar
                       </p>
                        <p className={' little-title'}>
                            Konditsionerlar muddatli to'lovga eng yaxshi narxlarda
                        </p>
                        <img className={'top-card-img'} src="/images/topPost/kondisioner.png" alt="error"/>
                        <button className='more-details-button button'>
                            Batafsil
                        </button>
                    </div>
                </div>
            </>
        )
    }

}
export default TopCard