import React from 'react';
import './ServicesCard.css';
import {
    Row,
    Col,
} from 'antd';
import {
    FaTruck,
    FaShieldAlt,
    FaCreditCard,
    FaPhone,
    FaSync,
} from 'react-icons/fa'


class ServicesCard extends React.Component {
    render() {
        return (
            <>
              <div className="main-service">
                  <div className="container">
                      <div className="service-card">
                          <p className="title-service-card">
                              Nima uchun Olcha.uz?
                          </p>
                          <Row className="p10 servises-hover">
                              <Col span={4} >
                                  <FaTruck className={'delivery'}/>
                              </Col>
                              <Col span={20} >
                                  <p className="delivery">
                                      Yetkazib berish
                                  </p>
                              </Col>
                          </Row>
                          <Row className="p10 servises-hover">
                              <Col span={4} >
                                  <FaShieldAlt className={'delivery'}/>
                              </Col>
                              <Col span={20} >
                                  <p className="delivery">
                                      Sifat Kafolati
                                  </p>
                              </Col>
                          </Row>
                          <Row className="p10 servises-hover">
                              <Col span={4} >
                                  <FaCreditCard className={'delivery'}/>
                              </Col>
                              <Col span={20} >
                                  <p className="delivery">
                                      Oson to'lov
                                  </p>
                              </Col>
                          </Row>
                          <Row className="p10 servises-hover">
                              <Col span={4} >
                                  <FaPhone className={'delivery'}/>
                              </Col>
                              <Col span={20} >
                                  <p className="delivery">
                                      Qo'llab-quvvatlash markazi
                                  </p>
                              </Col>
                          </Row>
                          <Row className="p10 servises-hover ">
                              <Col span={4} >
                                  <FaSync className={'delivery'}/>
                              </Col>
                              <Col span={20} >
                                  <p className="delivery">
                                      Eco-friendly
                                  </p>
                              </Col>
                          </Row>
                          <div className="last">

                          </div>
                      </div>

                  </div>
              </div>
            </>
        )
    }

} export default ServicesCard