import React from 'react';
import './Carousel.css';
import '../../../../styles/global.css';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {
    Row,
    Col,
} from 'antd';
class CarouselComponent extends React.Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
        this.slider.slickNext();
    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        var settings = {
            dots: false,
            infinite: true,
            speed: 500,
            autoplay:true,
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: 0,
            // responsive: [
            //     {
            //         breakpoint: 1024,
            //         settings: {
            //             slidesToShow: 3,
            //             slidesToScroll: 3,
            //             infinite: true,
            //             dots: true
            //         }
            //     },
            //     {
            //         breakpoint: 600,
            //         settings: {
            //             slidesToShow: 2,
            //             slidesToScroll: 2,
            //             initialSlide: 2
            //         }
            //     },
            //     {
            //         breakpoint: 480,
            //         settings: {
            //             slidesToShow: 1,
            //             slidesToScroll: 1
            //         }
            //     }
            // ]
        };
        return(
            <>
             <div className="container">
                 <div className="big-post p5">
                     <div className="carousel-content">
                         <Slider {...settings}>
                             <div className={'slider-item'}>
                                 <Row>
                                     <Col md={12} sm={24} xs={24}>
                                         <img style={{width:'100%'}} src="/images/mainSlider/phone.png" alt="error"/>
                                     </Col>
                                     <Col md={12} sm={24} xs={24}>
                                         <p className={'carousel-title'}>Realme 8 Pro smartfonini chegirma
                                             bilan sotib olishingiz mumkin!</p>
                                         <h5> Ekranda butun koinot</h5>
                                         <buttn className="buy-button button">
                                             Sotib olish
                                         </buttn>
                                     </Col>
                                 </Row>
                             </div>
                             <div className={'slider-item'}>
                                 <Row>
                                     <Col md={12} sm={24} xs={24}>
                                         <img style={{width:'100%'}} src="/images/mainSlider/airpods.png" alt="error"/>
                                     </Col>
                                     <Col md={12} sm={24} xs={24}>
                                         <p className={'carousel-title'}>Xiaomi hayratda qoldirishda davom etmoqda!</p>
                                         <h5> Airpods 3 allaqachon sotuvda</h5>
                                         <buttn className="buy-button button">
                                             Sotib olish
                                         </buttn>
                                     </Col>
                                 </Row>
                             </div>
                             <div className={'slider-item'}>
                                 <Row>
                                     <Col md={12} sm={24} xs={24}>
                                         <img style={{width:'100%'}} src='/images/mainSlider/texnik.png' alt="error"/>
                                     </Col>
                                     <Col md={12} sm={24} xs={24}>
                                         <p className={'carousel-title'}>Avto jihozlar</p>
                                         <h5>Butun o'zbekistob bo'ylab yetkazib beramiz</h5>
                                         <buttn className="buy-button button">
                                             Sotib olish
                                         </buttn>
                                     </Col>
                                 </Row>
                             </div>


                         </Slider>
                     </div>
                 </div>
             </div>
            </>
        )
    }

}
export default CarouselComponent