import React from 'react';
import './horizontalMenu.css'
import {
    Row,
    Col,
    Switch,
    Input,
    Badge,
} from "antd";
import {
    FaBars,
    FaSearch,
    FaUser,
    FaHeart,
    FaCartArrowDown,
} from 'react-icons/fa';
import {FirstMenuList, SecondMenuList} from "../../constants/menu";

class HorizontalMenu extends React.Component {
    render() {
        return (
            <>
              <div className="main">
                  <div className="container">
                      <div className={'top-menu'}>
                          <Row>
                              <Col md={12} sm={24}>
                                  <ul className="language-ul">
                                      <li>
                                          <div className='uzb-li'>
                                              O'zb
                                          </div>
                                      </li>
                                      <li>
                                          <div>
                                              Рус
                                          </div>
                                      </li>

                                  </ul>
                              </Col>
                              <Col md={12} sm={24}>
                                  <ul className="menu-list">
                                      {
                                          FirstMenuList.map(item=>{
                                              return  <li>
                                                  {item.title}
                                              </li>
                                          })
                                      }

                                      <li>
                                          <Switch className={'switch'} defaultChecked onChange={''}/>
                                      </li>
                                  </ul>
                              </Col>
                          </Row>
                      </div>
                      <div className="catalogs">
                          <Row>
                              <Col md={4} sm={6} xs={24}>
                                  <img width={'90%'} src="https://olcha.uz/dist/images/logo.svg" alt="logo"/>
                              </Col>
                              <Col md={3} sm={6} xs={24}>
                                  <button className="catalog-button">
                                      <Row>
                                          <Col span={8}>
                                              <span><FaBars style={{marginTop:'4px'}}/></span>
                                          </Col>
                                          <Col span={16}>Catalog</Col>
                                      </Row>
                                  </button>
                              </Col>
                              <Col md={9} sm={12} xs={24}>
                                  <div className="search">
                                      <Row>
                                          <Col span={20}>
                                              <Input className="search-input" placeholder={'Mahsulotlar aro qidiruv'} />
                                          </Col>
                                          <Col span={4}>
                                              <div className={'search-icon'}>
                                                  <FaSearch style={{fontSize:'25px'}}/>
                                              </div>
                                          </Col>
                                      </Row>

                                  </div>
                              </Col>
                              <Col md={8} sm={24} xs={24}>
                                  <Row>
                                      <Col span={8}>
                                          <button className="catalog-button user-button">
                                              <Row>
                                                  <Col span={8}>
                                                      <span><FaUser style={{marginTop:'4px'}}/></span>
                                                  </Col>
                                                  <Col span={16}>Kirish</Col>
                                              </Row>
                                          </button>
                                      </Col>
                                      <Col span={8}>
                                          <button className="catalog-button user-button">
                                              <Row>
                                                  <Col span={8}>
                                                      <span><FaHeart style={{marginTop:'4px'}}/></span>
                                                  </Col>
                                                  <Col span={16}>Tanlangan</Col>
                                              </Row>
                                          </button>
                                      </Col>
                                      <Col span={8}>
                                          <Badge count={5} offset={[-5,0]}>
                                              <button className="catalog-button user-button">
                                                  <Row>
                                                      <Col span={8}>
                                                          <span><FaCartArrowDown style={{marginTop:'4px'}}/></span>
                                                      </Col>
                                                      <Col span={16}>Savatcha</Col>
                                                  </Row>
                                              </button>
                                          </Badge>

                                      </Col>
                                  </Row>

                              </Col>
                          </Row>
                      </div>
                      <div className="category-list">
                          <ul>
                              {
                                  SecondMenuList.map(item=>{
                                      return <li>
                                          {item.title}
                                      </li>
                                  })
                              }


                          </ul>
                      </div>
                  </div>
              </div>


            </>
        )
    }

}

export default HorizontalMenu