import React from 'react';
import './popularProducts.css'
import {Row, Col} from "antd";
import {Products} from "../../../../constants/products";
import Slider from "react-slick";
import {Card} from 'antd';
import {
    FaStar,
} from 'react-icons/fa';


const {Meta } = Card;
class PopularProducts extends React.Component{
    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
    }
    next() {
        this.slider.slickNext();
    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            autoplay:false,
            slidesToShow: 5,
            slidesToScroll: 1,
            initialSlide: 0,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 1,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return(
            <>
                <div className="container">
                    <Row>
                        <p className="most-title">
                            Eng ko'p tanlangan mahsulotlar
                        </p>

                       <Col span={24} className={'p10'}>
                           <Slider {...settings}>
                               {
                                   Products.map(item=>{
                                       return <div className={'slider-item p10'}>
                                           <Card
                                               className={'p10'}
                                               hoverable
                                               style={{ width: '100%',borderRadius:'10px'}}
                                               cover={<img width={'100%'} alt={item.name} src={item.imgUrl} />}
                                           >
                                               <Meta description={item.brand} />
                                               <p style={{minHeight:'90px',fontSize:'16px'}}>{item.name}</p>


                                               {
                                                  item.datarating===4?  <table>
                                                      <tr>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                      </tr>
                                                  </table>:item.datarating===3?<table>
                                                      <tr>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                      </tr>
                                                  </table>: item.datarating===2?<table>
                                                      <tr>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                      </tr>
                                                  </table>:item.datarating===1?<table>
                                                      <tr>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                      </tr>
                                                  </table>:item.datarating===null?<table>
                                                      <tr>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                          <th><FaStar style={{color:'grey'}}/></th>
                                                      </tr>
                                                  </table>: <table>
                                                      <tr>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                          <th><FaStar style={{color:'#FF7C0B'}}/></th>
                                                      </tr>
                                                  </table>
                                               }
                                               <p style={{color:'grey',fontSize:'13px'}}>
                                                   {item.creditPrice} so'm/oy
                                               </p>
                                               <del>
                                                   <p style={{color:'red',fontSize:'15px'}}>{item.lastPrice===''? <br/>:item.lastPrice+' so\'m'}</p>

                                               </del>
                                               <p style={{fontWeight:600}}>{item.newPrice} so'm</p>

                                           </Card>
                                       </div>
                                   })
                               }



                           </Slider>

                       </Col>
                    </Row>
                </div>

            </>
        )
    }

}
export default PopularProducts