import React from 'react';
import CarouselComponent from "./components/Carousel/Carousel";
import {Row, Col} from "antd";
import TopCard from "./components/topCard/topCard";
import PopularProducts from "./components/popularProducts/popularProducts";
import NewProducts from "./components/newProducts/newProducts";
import Categories from "./components/categories/categories";
import CreditTopCard from "./components/creditTopCard/CreditTopCard";
import Discounts from "./components/disCounts/disCounts";
import CreditByDiscount from "./components/creditByDiscount/creditByDiscount";
import ServicesCard from "./components/servicesCard/ServicesCard";
import MayNeed from "./components/mayNeed/mayNeed";

class HomePage extends React.Component{
    render() {
        return(
            <>
                <div className="container">
                    <Row>
                        <Col className={'p5'} md={18} sm={24}>
                            <CarouselComponent />
                        </Col>
                        <Col className={'p5'} md={6} sm={24}>
                            <TopCard/>
                        </Col>
                        <Col className={'p10'} span={24}>
                            <PopularProducts/>
                        </Col>
                        <Col className={'p10'} span={24}>
                            <NewProducts/>
                        </Col>
                        <Col className={'p10'} span={24}>
                            <Categories/>
                        </Col>
                        <Col className={'p10'} md={6} sm={24}>
                            <CreditTopCard/>
                        </Col>
                        <Col className={'p10'} md={18} sm={24}>
                            <Discounts/>
                        </Col>
                        <Col className={'p10'} span={24}>
                            <CreditByDiscount/>
                        </Col>
                        <Col className={'p10'} md={8} sm={24}>
                            <ServicesCard/>
                        </Col>
                        <Col className={'p10'} md={16} sm={24}>
                            <MayNeed/>
                        </Col>
                    </Row>
                </div>

            </>
        )
    }

}
export default HomePage