import React from 'react';
import '../../../../styles/global.css'
import {
    Row,
    Col,
    Card,
} from 'antd';
import {Products} from "../../../../constants/products";
import {
    FaStar,
} from 'react-icons/fa';

const {Meta} = Card;

class Discounts extends React.Component{
    render() {
        return(
            <>
                <div className="container">
                    <p className="most-title">
                    Chegirmalar
                </p>
                    <Row>


                        {
                            Products.map(item=>{
                                return <Col className={'p10'} lg={6} md={12} sm={24}>
                                    <Card
                                        hoverable
                                        style={{ width: '100%',borderRadius:'10px' }}
                                        cover={<img width={'100%'} alt={item.name} src={item.imgUrl} />}
                                    >
                                        <Meta description={item.brand} />
                                        <p style={{minHeight:'70px',fontSize:'16px'}}>{item.name}</p>


                                        <table>
                                            <tr>
                                                <th><FaStar style={{color:'grey'}}/></th>
                                                <th><FaStar style={{color:'grey'}}/></th>
                                                <th><FaStar style={{color:'grey'}}/></th>
                                                <th><FaStar style={{color:'grey'}}/></th>
                                                <th><FaStar style={{color:'grey'}}/></th>
                                            </tr>
                                        </table>
                                        <p style={{color:'grey',fontSize:'13px'}}>
                                            {item.creditPrice} so'm/oy
                                        </p>
                                        <p style={{fontWeight:600}}>{item.newPrice} so'm</p>

                                    </Card>
                                </Col>
                            })
                        }
                    </Row>
                </div>
            </>
        )
    }

}
export default Discounts